*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 22px;
}
body{
  min-height:100vh;
  border: 1px solid green;
  margin: 0 10%;
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  margin: auto;
}

header{
  width: 100%;
  padding: 0 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: green;
  color: white;
}
main{
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}
footer{
  width: 100%;
  padding: 0 0.25rem;
  background-color: green;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul{
  width: 100%;
  list-style: none;
  padding: 0 0.25rem 0.25rem;
}
ul li::before{
  content: "\200B";
}

.item{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem 0 0.5rem 0.5rem;
  margin: 0.25rem 0;
  background-color: #eee;
}
.item :first-child{
  margin: 0;
}
.item input[type="checkbox"]{
  text-align: center;
  width: 2.5rem;
  width: 48px;
  min-width: 48px;
  height: 2.5rem;
  height: 38px;
  min-height: 38px;
  cursor: pointer;
  margin-right: 0.5rem;

}
.item input[type="checkbox"]:focus+label{
  text-decoration: underline;
}
.item label{
  font-size: 30px;
  flex-grow: 1;
}
.item svg{
  width: 48px;
  min-width: 48px;
  height: 30px;
  color: steelblue;
  cursor: pointer;
  outline: none;
  transition: 1s;
}
.item svg:hover{
  transform: scale(0.9);
  color: red;
}
.addform{
  font-size: 30px;
  padding:0.5rem;
}
.addform label{
  position: absolute;
  left: -99999px;
}
.addform button{
  width: 40px;
  height: 40px;
  background-color:aliceblue;
  border: 1px solid black;
  color: green;
  border-radius: 5px;
  cursor: pointer;
}
.addform button:hover{
  background-color: lightblue;
  color: white;
}
.addform input[type="text"]{
  flex-grow: 1;
  max-width: calc(100%-50px);
  padding: 0.25rem;
  margin-right: 0.25rem; 
  font-size: 1rem; 
  outline: none;
  border-radius: 5px;
  max-height: 48px;
}
.searchitem label{
  position: absolute;
  left: -99999px;
}
.searchitem input[type="text"]{
  flex-grow: 1;
  max-width: calc(100%-50px);
  padding: 0.25rem;
  margin-right: 2rem; 
  font-size: 1rem; 
  outline: none;
  border-radius: 5px;
  max-height: 48px;
}